<script lang="ts">
  import { onMount, onDestroy } from "svelte";
  import {
    chatStore,
    addProduct,
    removeProduct,
    setDocType,
    resetFilters,
  } from "../stores/chat";
  import { PRODUCTS, DOC_TYPES } from "../types";

  let isProductDropdownOpen = false;
  let isDocTypeDropdownOpen = false;
  let filterRef: HTMLDivElement;
  let docTypeFilterRef: HTMLDivElement;

  // Handle click outside
  function handleClickOutside(event: MouseEvent) {
    const target = event.target as HTMLElement;

    // Check if click is outside the dropdowns
    if (filterRef && !filterRef.contains(target)) {
      isProductDropdownOpen = false;
    }
    if (docTypeFilterRef && !docTypeFilterRef.contains(target)) {
      isDocTypeDropdownOpen = false;
    }
  }

  // Handle widget click outside
  function handleWidgetClickOutside() {
    isProductDropdownOpen = false;
    isDocTypeDropdownOpen = false;
  }

  onMount(() => {
    // Listen for clicks on the document
    document.addEventListener("click", handleClickOutside, true);

    // Listen for widget click outside event
    const widget = document.querySelector("fluent-ai-chatbot");
    if (widget) {
      widget.addEventListener("click-outside", handleWidgetClickOutside);
    }
  });

  onDestroy(() => {
    // Clean up event listeners
    document.removeEventListener("click", handleClickOutside, true);

    const widget = document.querySelector("fluent-ai-chatbot");
    if (widget) {
      widget.removeEventListener("click-outside", handleWidgetClickOutside);
    }
  });

  // Get selected doc type label
  $: selectedDocTypeLabel =
    DOC_TYPES.find((type) => type.value === $chatStore.selectedDocType)
      ?.label || "Select Doc Type";
</script>

<div class="filter-container">
  <div class="filter-wrapper">
    <!-- Doc Type Filter -->
    <div class="dropdown-container" bind:this={docTypeFilterRef}>
      <button
        class="filter-button"
        on:click={() => (isDocTypeDropdownOpen = !isDocTypeDropdownOpen)}
      >
        <span>{selectedDocTypeLabel}</span>
        <svg
          class="dropdown-icon {isDocTypeDropdownOpen ? 'rotate' : ''}"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M19 9l-7 7-7-7"
          />
        </svg>
      </button>

      {#if isDocTypeDropdownOpen}
        <div class="dropdown-menu left">
          <div class="dropdown-content">
            {#each DOC_TYPES as type}
              <button
                class="dropdown-item {$chatStore.selectedDocType === type.value
                  ? 'selected'
                  : ''}"
                on:click={() => {
                  setDocType(type.value);
                  isDocTypeDropdownOpen = false;
                }}
              >
                {type.label}
                {#if $chatStore.selectedDocType === type.value}
                  <svg
                    class="check-icon"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      stroke-width="2"
                      d="M5 13l4 4L19 7"
                    />
                  </svg>
                {/if}
              </button>
            {/each}
          </div>
        </div>
      {/if}
    </div>

    <!-- Product Filter -->
    <div class="dropdown-container" bind:this={filterRef}>
      <button
        class="filter-button"
        on:click={() => (isProductDropdownOpen = !isProductDropdownOpen)}
      >
        <span>Select Products</span>
        <svg
          class="dropdown-icon {isProductDropdownOpen ? 'rotate' : ''}"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M19 9l-7 7-7-7"
          />
        </svg>
      </button>

      {#if isProductDropdownOpen}
        <div class="dropdown-menu right">
          <div class="dropdown-content product-list">
            {#each PRODUCTS as product}
              <div class="checkbox-item">
                <label class="checkbox-label">
                  <input
                    type="checkbox"
                    checked={$chatStore.selectedProducts.includes(
                      product.value
                    )}
                    on:change={() => {
                      if ($chatStore.selectedProducts.includes(product.value)) {
                        removeProduct(product.value);
                      } else {
                        addProduct(product.value);
                      }
                    }}
                    class="checkbox"
                  />
                  <span class="checkbox-text">{product.label}</span>
                </label>
              </div>
            {/each}
          </div>
        </div>
      {/if}
    </div>
  </div>

  <!-- Selected Products Tags -->
  {#if $chatStore.selectedProducts.length > 0}
    <div class="tag-container">
      {#each $chatStore.selectedProducts as value}
        {@const product = PRODUCTS.find((p) => p.value === value)}
        {#if product}
          <span class="tag">
            {product.label}
            <button
              on:click={() => removeProduct(value)}
              class="tag-remove"
              aria-label="Remove {product.label}"
            >
              <svg
                class="tag-icon"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M6 18L18 6M6 6l12 12"
                />
              </svg>
            </button>
          </span>
        {/if}
      {/each}
    </div>
  {/if}
</div>

