<script lang="ts">
  import { onMount } from "svelte";
  import {
    chatStore,
    selectChat,
    deleteChat,
    fetchChats,
  } from "../stores/chat";
  import { userStore } from "../stores/user";
  import type { Chat } from "../types";

  onMount(() => {
    const userId = $userStore.userId;
    if (userId) {
      fetchChats(userId);
    }
  });

  function handleChatSelect(chat: Chat) {
    selectChat(chat);
    $chatStore.showHistory = false;
  }

  function handleChatDelete(event: Event, chatId: string) {
    event.stopPropagation();
    deleteChat(chatId);
  }

  function formatDate(dateString: string): string {
    const date = new Date(dateString);
    return date.toLocaleDateString("en-US", {
      month: "short",
      day: "numeric",
      hour: "numeric",
      minute: "numeric",
    });
  }

  function handleKeyDown(event: KeyboardEvent, callback: () => void) {
    if (event.key === "Enter" || event.key === " ") {
      event.preventDefault();
      callback();
    }
  }
</script>

<div class="history-container">
  {#if $chatStore.isLoading}
    <div class="loading-container">
      <div class="loading-content">
        <div class="loading-dots">
          <div class="dot dot-1"></div>
          <div class="dot dot-2"></div>
          <div class="dot dot-3"></div>
        </div>
        <p class="loading-text">Loading chat history...</p>
      </div>
    </div>
  {:else}
    <div class="chat-list">
      {#if $chatStore.chats.length === 0}
        <div class="empty-state">
          <div class="empty-message">No chats found</div>
        </div>
      {:else}
        {#each $chatStore.chats as chat (chat.id)}
          <div
            role="button"
            tabindex="0"
            class="chat-item"
            on:click={() => handleChatSelect(chat)}
            on:keydown={(e) => handleKeyDown(e, () => handleChatSelect(chat))}
          >
            <div class="chat-item-content">
              <div class="chat-info">
                <h3 class="chat-title">{chat.title}</h3>
                <p class="chat-date">{formatDate(chat.created_at)}</p>
              </div>
              <button
                type="button"
                class="delete-button"
                on:click={(e) => handleChatDelete(e, chat.id)}
                aria-label="Delete chat"
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  class="delete-icon"
                  viewBox="0 0 20 20"
                  fill="currentColor"
                >
                  <path
                    fill-rule="evenodd"
                    d="M9 2a1 1 0 00-.894.553L7.382 4H4a1 1 0 000 2v10a2 2 0 002 2h8a2 2 0 002-2V6a1 1 0 100-2h-3.382l-.724-1.447A1 1 0 0011 2H9zM7 8a1 1 0 012 0v6a1 1 0 11-2 0V8zm5-1a1 1 0 00-1 1v6a1 1 0 102 0V8a1 1 0 00-1-1z"
                    clip-rule="evenodd"
                  />
                </svg>
              </button>
            </div>
          </div>
        {/each}
      {/if}
    </div>
  {/if}
</div>

